import {
    FCM_TOKEN_GENERATED
} from "../actions/users";

export function fcm(state = {
    fcmToken: "AAAAeEMbCKY:APA91bG9S31QDw0rE1-1p1cWSZsKiWAx8mZKUGIl9O5SQBIN_ciXib4cAjl_2v4683qFAzbHxal5tK1Bt0_iz9t5vNsBIwkF1y_Z0P_f0bm_B96U1RivbYYNmDQXt2wXmiEen2X6SWF9"
}, action) {
    switch (action.type) {

        case FCM_TOKEN_GENERATED:
            return Object.assign({},state,{
                fcmToken: action.fcmToken
            });

        default:
            return state;
    }
}


